.teacherAdd {
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 100vh;
    background: url(../../../assets/images/login_img.jpg) no-repeat;
    background-size:cover;
    position: relative;
  }
  
  .teacherAdd__form {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 280px;
    color: white;
  }
  
  .teacherAdd__form h1 {
    font-family: sans-serif;
    color: white;
    font-size: 40px;
    border-bottom: 6px solid #34a851;
    margin-bottom: 30px;
    width: 130px;
    padding: 13px 0;
  }
  .text_box {
    width: 100%;
    overflow: hidden;
    color: white;
    background: none;
    border-bottom: 1px solid #34a851;
    margin: 8px 0;
    padding: 8px 0;
  }
  input {
    background: none;
    outline: none;
    border: none;
    font-size: 15px;
    color: white;
    font-family: sans-serif;
    margin: 0 10px;
  }
  
  input.btn {
    width: 280px;
    height: 40px;
    color: white;
    font-size: 20px;
    margin: 15px 0;
    border: 2px solid #34a851;
    border-radius: 4px;
    cursor: pointer;
    transition: 0.5s;
  }
  
  input.btn:hover {
    color: white;
    background: #34a851;
  }
  
  